* {
  margin: 0;
  padding: 0;
  border-radius: 0;
}

body {
  font-family: "Mulish", sans-serif;
  -webkit-font-smoothing: antialiased;
  background-color: #0B1B32;
  color: #f1f1f1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
